import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllProducts } from "./actions";
import { Link } from "@reach/router";
import Modal from "./components/Modal";
import { useRef } from "react";
import AddProduct from "./AddProduct";

export default function ProductList() {
  const dispatch = useDispatch();
  const [state, setstate] = useState({
    search: "",
  });
  useEffect(() => {
    dispatch(fetchAllProducts());
  }, [dispatch]);
  const availableProducts = useSelector(
    (state) => state.products.availableProducts
  );
  console.log(availableProducts);
  const editModal = useRef();
  const openModal = (content) => {
    editModal.current.open(content);
  };
  const closeModal = () => {
    editModal.current.close();
  };
  return (
    <div className="p-10">
      <Modal ref={editModal} />
      <div className="p-10 flex flex-col">
        <span className="text-center font-medium">Filtrer </span>
        <input
          onChange={(e) => {
            setstate({ ...state, search: e.target.value });
          }}
          type="text"
          className="w-full  p-2 border-2"
        />
      </div>
      <div className="flex justify-end my-3">
        <Link className="p-2 text-white bg-blue-400" to="/ajouter">
          Ajouter un produit
        </Link>
      </div>
      <div className="flex justify-center">
        <table>
          <thead>
            <tr>
              <td className="border-2 p-1 font-medium border-gray-500">N</td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Image
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Code Barre
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Nom Arabe
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Nom Francais
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Marque
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Categorie
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Contenance
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Taille / poids
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Unite de mesure / emballage
              </td>
              <td className="border-2 p-2 font-medium border-gray-500">
                Actions
              </td>
            </tr>
          </thead>
          <tbody>
            {availableProducts
              .filter(
                (prod) =>
                  prod.keywords
                    ?.toLowerCase()
                    .includes(state.search.toLowerCase()) ||
                  String(prod.barcode)?.includes(state.search.toLowerCase())
              )
              .map((product, i) => {
                return (
                  <tr key={`${product.name}${product.img_src}`}>
                    <td className="border-2 p-1 border-gray-500">{i + 1}</td>
                    <td className="border-2 p-2 border-gray-500">
                      <img
                        onClick={() => {
                          openModal(
                            <div>
                              <button
                                className="absolute"
                                style={{ top: "5%", right: "5%" }}
                                onClick={closeModal}>
                                X
                              </button>
                              <div className="flex justify-center items-center">
                                <img src={product.img_src} alt={product.name} />
                              </div>
                            </div>
                          );
                        }}
                        className="w-12 cursor-pointer"
                        src={product.img_src}
                        alt={product.name}
                      />
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {product.barcode}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {product.name}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {product.frName}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {product.brand}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {JSON.parse(product.category)["name"]}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      <div className="flex">
                        <span>{JSON.parse(product.contentUnit)["name"]}</span>
                        <span>{product.contentNumber}</span>
                      </div>
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      <div className="flex">
                        <span>{JSON.parse(product.unit)["name"]}</span>
                        <span>{product.productContenance}</span>
                      </div>
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      {JSON.parse(product.packageName)["name"]}
                    </td>
                    <td className="border-2 p-2 border-gray-500">
                      <button
                        onClick={() => {
                          openModal(
                            <AddProduct
                              edit={true}
                              product={product}
                              closeM={closeModal}
                            />
                          );
                        }}
                        className="bg-green-400 text-white p-1 cursor-pointer hover:bg-green-600 rounded-lg ">
                        Modifier
                      </button>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </div>
  );
}
